inp-color-group,
inp-number-group,
inp-range-group,
inp-rotation-group,
inp-select-group,
inp-switch-group,
inp-text-group,
inp-vector3-group {
    display: block;
    margin-bottom: 10px;
}

$tabs-color: #fff;
.panel {
    max-width: 380px;
    min-width: 380px;
    width: 380px;
    position: absolute;
    background: #20242d;
    transition: all 0.2s;
    top: 56px;
    left: 0;
    bottom: 0;
    display: flex;
    flex: auto;
    flex-direction: column;
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: #fff;
    }
    .panel-header {
        height: 48px;
        display: flex;
        flex: none;
        width: 100%;
        align-items: center;
        align-content: center;
        h3 {
            color: #fff;
            // line-height: 48px;
            font-size: 18px;
            margin: 0;
            padding-left: 12px;
        }
        .btn-close {
            cursor: pointer;
            margin-left: auto;
            margin-right: 9px;
            font-size: 24px;
            color: #fafafa;
            // background: #88231f;
            margin-right: 12px;
            outline: none;
            border: none;
            &:hover {
                // background-color: #c02b2b;
                color: #fff;
            }
        }
    }
    .panel-body {
        display: flex;
        flex: none;
        height: calc(100% - 92px);
        width: 100%;
        background-color: #363d4b;
        color: #fff;
        overflow-y: hidden;
        & > div,
        .w100 {
            width: 100%;
        }
        .ant-divider-horizontal.ant-divider-with-text {
            color: #fff;
        }
        .store {
            position: relative;
            display: block;
            flex-wrap: wrap;
            // height: calc(100% - 88px);
            height: 100%;
            width: 100%;
            padding: 0 12px 0 16px;
            overflow-y: scroll;
            &--expand {
                height: auto;
            }

            &::-webkit-scrollbar {
                width: 6px;
                background-color: transparent;
            }

            &::-webkit-scrollbar-track {
                box-shadow: none;
                background-color: transparent;
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
                background-color: #555;
            }
        }
        .geometry-inputs,
        .material-inputs,
        .prop-inputs {
            h3 {
                margin-bottom: 10px;
            }
            // padding-top: 15px;
            // padding-bottom: 15px;
            .input-group {
                // border-bottom: 1px solid rgb(158, 158, 158);
            }
        }
    }
    .panel-footer {
        height: 52px;
        display: flex;
        flex: none;
        width: 100%;
        align-items: center;
        align-content: center;
        margin-top: -18px;
        background: #20242d;
        z-index: 100;
        h3 {
            color: #fff;
            // line-height: 48px;
            font-size: 18px;
            margin: 0;
            padding-left: 12px;
        }
        .buttons {
            cursor: pointer;
            margin-left: auto;
            margin-right: 9px;
            font-size: 24px;
            color: #fafafa;
            // background: #88231f;
            margin-right: 12px;
            &:hover {
                // background-color: #c02b2b;
                color: #fff;
            }
        }
    }

    &.inner-layout {
        height: 100%;

        .ant-input-affix-wrapper {
            padding: 2px 12px 2px 12px;
            border-radius: 5px;
        }

        .ant-tabs {
            height: 100%;

            .tab-inner {
                padding: 24px 4px 0 0px !important;
                height: 100%;

                .store {
                    position: relative;
                    display: block;
                    flex-wrap: wrap;
                    height: 100%;
                    width: 100%;
                    padding: 0 12px 0 16px;
                    overflow-y: scroll;
                    &--expand {
                        height: auto;
                    }
                }

                .store {
                    &::-webkit-scrollbar {
                        width: 6px;
                        background-color: transparent;
                    }

                    &::-webkit-scrollbar-track {
                        box-shadow: none;
                        background-color: transparent;
                    }

                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
                        background-color: #555;
                    }
                }
            }

            &-ink-bar {
                background-color: $tabs-color;
                border-right-width: 2px;
                display: none !important;
            }
        }
    }
}

.ant-layout-sider-dark,
.panel {
    .ant-radio-wrapper {
        color: #fff;
    }
}
